<template>
  <div id="all-patients">
    <div class="tools-top flex-row">
      <div class="search border-r flex-row">
        <input type="text" v-model="patientName" placeholder="搜索患者姓名" @keyup.enter="searchPatientName"/>
        <div class="search-btn" @click.stop="searchPatientName"></div>
      </div>
      <div class="search flex-row">
        <span style="color: rgba(0,0,0,.54); margin-right: 8px;">入组时间</span>
        <datepicker class="date" v-model="startJoinTime" :width="170"></datepicker>
        <span style="color: rgba(0,0,0,.54); margin: 0 8px;">至</span>
        <datepicker class="date" v-model="endJoinTime" :width="170"></datepicker>
        <div class="search-btn" @click.stop="searchPatientJointime" style="border: 1px solid rgba(0, 0, 0, .12); border-left: none;"></div>                        
      </div>
    </div>
    <div class="tools-middle flex-row">
      <div class="btn-gray">
        <input type="checkbox" id="all" @click="allChecked"/>
        <label for="all">全选</label>
      </div>
      <div class="flex-row">
        <div class="btn-gray mg-r-8" :class="{disabled: disabled}" @click="deletePatientsDialog = !disabled">删除</div>
        <div class="btn-gray mg-r-8" :class="{disabled: disabled}" @click="">锁定</div>
        <div class="btn-gray mg-r-8" :class="{disabled: disabled}" @click="">解锁</div>
        <div class="btn-gray mg-r-8" :class="{disabled: disabled}" @click="shareGroupDialog = !disabled">分组</div>
        <div class="btn-gray mg-r-8" :class="{disabled: disabled}" style="width: 96px;" @click="deletePatientGroup">取消分组</div>  
        <div class="btn-gray mg-r-8" :class="{disabled: disabled}" style="width: 96px;" @click="exportPatient">导出数据</div>
        <select v-model="currentGroup" style="width: 160px; height: 32px;">
          <option value="0">全部</option>
          <option v-for="group in groupList" v-text="group.u_groupname" :value="group.u_groupid"></option>
        </select>
      </div>
      <!--将选中的患者添加到组-->
      <DialogBox v-if="shareGroupDialog" :width="352">
        <h3 slot="header">分组</h3>
        <div slot="body" class="flex-col" style="padding: 24px 32px 0">
          <!--搜索组-->
          <div class="flex-row" style="margin-bottom: 16px; position: relative;">
            <input type="text" placeholder="请输入组名搜索" v-model="groupName" style="flex: 1;" @keyup.enter="searchGroup"/>
            <div class="search-btn" style="right: 0;" @click.stop="searchGroup"></div>
          </div>
          <!--组列表-->
          <div v-for="group in groupList" class="flex-row"  style="align-items: center; justify-content: space-between;">
            <div class="flex-row" style="align-items: center;">
              <div v-if="!manageGroupFalg">
                <input type="checkbox" :id="group.u_groupid" :value="group.u_groupid" v-model="checkGroup"/>
                <label :for="group.u_groupid"></label>
              </div>
              <div class="flex-col">
                <input :value="group.u_groupname" :readonly="!group.edit" v-model="group.u_groupname" :style="{'border-color': group.edit ? '#ccc' : '#fff'}">
                <div v-if="group.edit" style="margin-top: 5px">
                  <div class="btn-blue" @click="editGroupName(group.u_groupid, group.u_groupname, group.edit)">确定</div>                        
                  <div class="btn-gray" @click="group.edit = false">取消</div>                        
                </div>
              </div>
            </div>
            <div v-if="!group.edit && manageGroupFalg" class="flex-row">
              <span style="color: #458df1; cursor: pointer; margin-right: 20px;" @click="group.edit = true">修改</span>                        
              <span style="color: #e51c23; cursor: pointer;" @click="deleteGroup(group.u_groupid)">删除</span>                        
            </div>
          </div>
          <!--新建分组-->
          <div style="margin-top: 20px;">
            <div class="btn-gray" style="width: 96px;" @click="newGroupFlag = !newGroupFlag">{{newGroupFlag ? '取消新建' : '新建分组'}}</div>
            <div class="btn-gray" style="width: 96px;" @click="manageGroupFalg = !manageGroupFalg">{{manageGroupFalg ? '取消管理' : '管理分组'}}</div> 
            <div class="flex-row" v-if="newGroupFlag" style="margin-top: 10px;">
              <input type="text" placeholder="输入新建组名" style="flex: 1;" v-model="newGroupName" @keyup.enter="createGroup"/>
              <label style="background: #468df1; color: #fff; width: 48px; line-height: 38px;text-align: center;" @click="createGroup">新建</label>
            </div>                                            
          </div>
        </div>
        <div slot="footer" v-if="!newGroupFlag && !manageGroupFalg">
          <div class="btn-blue" @click="addPatientGroup">确定</div>
          <div class="btn-gray" @click="shareGroupDialog = false">取消</div>
        </div>
      </DialogBox>
      <!--删除患者对话框-->
      <DialogBox v-if="deletePatientsDialog">
        <h3 slot="header">删除患者</h3>
        <div slot="body" style="padding-left: 32px;">
            您确定要删除选定的患者吗？
        </div>
        <div slot="footer" style="text-align: right;">
          <div class="btn-gray" @click="deletePatients">确定</div>
          <div class="btn-blue" @click="deletePatientsDialog = false">取消</div>
        </div>
      </DialogBox>
    </div>
    <!--患者列表-->
    <div id="table">
      <div class="table-tr table-header">
        <div class="w28">
          <input type="checkbox" />
          <label style="visibility: hidden;"></label>
        </div>
        <div class="w40">ID</div>   
        <div class="w80">姓名</div>
        <div class="w50">性别</div>
        <div class="w55">年龄</div>
        <div class="w105">入组时间</div>
        <div class="w105">更新时间</div>
        <div class="w85">状态</div>
        <div class="w80">录入人</div>
        <div style="flex: 1;">组</div>
        <div class="w210">操作</div>
      </div>
      <div class="table-tr" v-for="(item,index) in currentPagePatiens" :key="item.u_MDID">
        <div class="w28">
          <input type="checkbox" v-model="checkList" :id="item.u_MDID" :value="item.u_MDID"/>
          <label :for="item.u_MDID"></label>
        </div>
        <div class="w40" v-text="index + 1"></div>               
        <!--'/project/'+ projectid + '/patient_list/' + currentGroup + '/' + currentPage +'/patient_detail/'+item.u_MDID-->
        <div class="w80"><router-link :to="{name: 'patientDetail', params:{'projectid': projectid, 'groupid': currentGroup, 'pagenum': currentPage, 'MDID': item.u_MDID}}" v-text="item.u_patientname"></router-link></div>
        <div class="w50" v-text="item.u_gender == '' ? '' : item.u_gender == '1' ? '男' : '女'"></div>
        <div class="w55">{{ date - Number(item.u_birthday.slice(0,4)) }}岁</div>
        <div class="w105" v-text="item.u_jointime"></div>
        <div class="w105" v-text="item.u_createtime.slice(0,10)"></div>
        <div class="w85">{{ item.follow }}</div>
        <div class="w80" v-text="item.s_username"></div>
        <div style="flex: 1;" v-text="item.group"></div>
        <div class="w210 flex-row">
          <span class="mg-r-20 red" @click="deletePatientsDialog = true, checkList.push(item.u_MDID)" v-if="currentGroup === '0'">删除</span>  
          <span class="mg-r-20">锁定</span>       
          <span class="mg-r-20" @click="shareGroupDialog = true, checkList.push(item.u_MDID)">分组</span>   
          <span style="cursor: pointer;" @click="deletePatientGroup(item.u_MDID)">取消分组</span>       
        </div>
      </div>
      <div class="page" v-if="Math.ceil(allPatientNum/30) > 1">
        <Page :total="allPatientNum" :current="currentPage" :pagesize="30" @changepage="changePage"></Page>
      </div>
    </div>
  </div>
</template>
<script>
  // import $ from 'webpack-zepto'
  import DialogBox from '../lib/dialog.vue'
  import Page from '../lib/page.vue'
  import API from '../api.js'
  import Datepicker from '../lib/Date.vue'
  export default {
    data () {
      return {
        projectid: this.$route.params.projectid,
        allPatientNum: 0,
        currentPage: 1,
        currentPagePatiens: [],
        patientName: '',
        startJoinTime: '',
        endJoinTime: '',
        checkList: [],
        disabled: true,
        allCheck: false,
        groupList: [],
        checkGroup: [],
        currentGroup: this.$route.params.groupid,
        shareGroupDialog: false,
        groupName: '',
        newGroupName: '',
        newGroupFlag: false,
        manageGroupFalg: false,
        date: new Date().getFullYear(),
        deletePatientsDialog: false
      }
    },
    components: {
      DialogBox,
      Page,
      Datepicker
    },
    created () {
      this.getGroupList()
      this.currentPage = parseInt(this.$route.params.pagenum ? this.$route.params.pagenum : 1)
      this.loadPatientList()
    },
    watch: {
      '$route.params.pagenum': function (val) {
        this.currentPage = parseInt(this.$route.params.pagenum ? this.$route.params.pagenum : 1)
        this.loadPatientList()
      },
      'currentGroup': function (val) {
        this.$router.push({name: 'patientList', params: {groupid: val, pagenum: 1}})
        this.loadPatientList()
      },
      'checkList': function (val) {
        val.length > 0 ? this.disabled = false : this.disabled = true
      }
    },
    methods: {
      // 翻页 改变路由
      changePage () {
        this.$router.push({name: 'patientList', params: {pagenum: arguments[0]}})
      },
      // 获取自定义分组列表
      getGroupList () {
        API.GetGroups({userid: this.$root.userid, projectid: this.$route.params.projectid}).then((response) => {
          this.groupList = []
          response.map(n => this.groupList.push({
            u_createtime: n.u_createtime,
            u_groupcreate: n.u_groupcreate,
            u_groupid: n.u_groupid,
            u_groupname: n.u_groupname,
            u_projectid: n.u_projectid,
            edit: false
          }))
        }).catch((err) => {
          console.log(err)
        })
      },
      // 加载该项目患者列表
      loadPatientList () {
        if (this.currentGroup === '0') {
          // 默认全部患者
          API.GetPatients({userid: this.$root.userid, projectid: this.$route.params.projectid, pagenum: this.currentPage, type: '1'}).then((response) => {
            this.currentPagePatiens = []
            this.allPatientNum = Number(response.allnum)
            response.patientList.map(n => this.currentPagePatiens.push(n))
          }).catch((err) => {
            console.log(err)
          })
        } else {
          // 获取分组下的患者列表
          API.GetGroupPatients({userid: this.$root.userid, groupid: this.currentGroup, pagenum: this.currentPage}).then((response) => {
            this.currentPagePatiens = []
            this.allPatientNum = Number(response.num)
            if (response.data.length === 0) return
            response.data.map(n => this.currentPagePatiens.push(n))
          }).catch((err) => {
            console.log(err)
          })
        }
      },
      /* 根据姓名搜索患者 */
      searchPatientName () {
        API.SearchPatientName({userid: this.$root.userid, projectid: this.$route.params.projectid, name: this.patientName}).then((response) => {
          this.currentPagePatiens = []
          response.map(n => this.currentPagePatiens.push(n))
          this.allPatientNum = this.currentPagePatiens.length
        }).catch((err) => {
          console.log(err)
        })
      },
      /* 根据入组时间段搜素患者 */
      searchPatientJointime () {
        API.SearchPatientJointime({userid: this.$root.userid, projectid: this.$route.params.projectid, startjoin: this.startJoinTime, endjoin: this.endJoinTime}).then((response) => {
          this.currentPagePatiens = []
          response.map(n => this.currentPagePatiens.push(n))
          this.allPatientNum = this.currentPagePatiens.length
        }).catch((err) => {
          console.log(err)
        })
      },
      // 导出患者数据
      exportPatient () {
        let url = '/patient/exportpatient?userid=' + this.$root.userid + '&mdid=' + this.checkList.join(',')
        process.env.NODE_ENV === 'development' ? url = '/api' + url : ''
        window.location.href = url
      },
      // 搜索分组
      searchGroup () {
        API.SearchGroup({userid: this.$root.userid, projectid: this.$route.params.projectid, groupname: this.groupName}).then((response) => {
          this.groupList = []
          response.map(n => this.groupList.push({
            u_createtime: n.u_createtime,
            u_groupcreate: n.u_groupcreate,
            u_groupid: n.u_groupid,
            u_groupname: n.u_groupname,
            u_projectid: n.u_projectid,
            edit: false
          }))
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 新建分组
      createGroup () {
        API.CreateGroup({userid: this.$root.userid, projectid: this.$route.params.projectid, groupname: this.newGroupName}).then((response) => {
          this.newGroupFlag = false
          this.getGroupList()
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 修改组名
      editGroupName (groupid, groupname) {
        API.EditGroupName({userid: this.$root.userid, groupid: groupid, groupname: groupname}).then((response) => {
          this.getGroupList()
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 删除组
      deleteGroup (groupid) {
        API.DeleteGroup({groupid: groupid}).then((response) => {
          this.getGroupList()
          this.loadPatientList()
        }).catch((err) => {
          window.alert(err)
        })
      },
      /* 添加patients 到 选中的组 */
      addPatientGroup () {
        this.shareGroupDialog = false
        API.AddGroupPatient({userid: this.$root.userid, groupid: this.checkGroup.join(','), patients: this.checkList.join(',')}).then((response) => {
          this.disabled = true
          this.checkGroup = []
          this.checkList = []
          this.loadPatientList()
        }).catch((err) => {
          console.log(err)
        })
      },
      /* 将选中的 patients 从 组中移除 */
      deletePatientGroup (mdid) {
        let groupid = this.$route.params.groupid === '0' ? '' : this.$route.params.groupid
        API.DeleteGroupPatient({groupid: groupid, patients: this.checkList.join(',') || mdid}).then((response) => {
          this.disabled = true
          this.checkGroup = []
          this.checkList = []
          this.loadPatientList()
        }).catch((err) => {
          console.log(err)
        })
      },
      /* 全选或全不选 */
      allChecked () {
        this.allCheck = !this.allCheck
        if (this.checkList.length !== this.currentPagePatiens.length) {
          this.checkList = []
          for (let n in this.currentPagePatiens) {
            this.checkList.push(this.currentPagePatiens[n].u_MDID)
          }
        } else {
          this.checkList = []
        }
      },
      /* 批量删除患者操作 */
      deletePatients () {
        API.DeletePatients({userid: this.$root.userid, mdid: this.checkList.join(',')}).then((response) => {
          this.loadPatientList(this.currentPage)
          this.deletePatientsDialog = false
          this.disabled = true
          this.toast({
            type: 'success',
            text: '删除患者成功！',
            placement: 'top'
          })
        }).catch((err) => {
          window.alert(err)
        })
      }
    }
  }
</script>
<style scoped>
  #all-patients{
    background: #fff;
  }
  /* tools */
  .tools-top {
    border-bottom: 1px solid rgba(0,0,0,.12);
    padding: 8px 0;
  }
  .tools-top .search {
    padding: 8px 32px;
    flex: 1;
    align-items: center;
    position: relative;
  }
  .tools-top .search input{
    flex: 1;
  }
  .tools-middle {
    padding: 16px 32px;
    justify-content: space-between;
  }
  .tools-middle .disabled{
    cursor: not-allowed;
    color: rgba(0,0,0,.26); 
  }
  /* 翻页按钮 */
  .page{
    padding: 16px 32px;
    text-align: right;
    border-top: 1px solid rgba(0,0,0,.12);
  }
</style>


