<template>
  <div id="home">
    <Navigation></Navigation>
    <div class="main">
      <div class="btn-blue" style="width: 88px; margin: 32px 0" @click="createProjectDialog = true">新建项目</div>
      <!--新建项目对话框-->
      <DialogBox v-if="createProjectDialog" :width = 600>
        <div slot="header">新建项目</div>
        <div slot="body" class="body">
          <div class="option flex-row">
            <label class="label">项目名称</label>
            <input type="text" v-model="projectName"/>
          </div>
          <div class="option flex-row">
            <label class="label">开始时间</label>
            <datepicker class="date" v-model="startTime" :width="128"></datepicker>
            <label class="label" style="width: 104px;">结束时间</label>
            <datepicker class="date" v-model="endTime" :width="128"></datepicker>            
          </div>
          <div class="option flex-row" style="align-items: flex-start;">
            <label class="label">项目简介</label>
            <textarea v-model="projectMem"></textarea>
          </div>
          <div class="option flex-row" style="margin-right: 72px;">
            <label class="label">任务表单</label>
            <select v-model="templateID" style="height: 40px; background-position: 365px center;">
              <option v-for="form in formList" :value="form.formid">{{form.name}}</option>
            </select>
            <!--<input type="text" class="flex" v-model="filename" readonly/>
            <input type="file" id="form" @change="handleFile('form')" hidden/>
            <label for="form" style="text-align: left; cursor: pointer;margin-left: 8px;"><img src="../assets/icon_svg/icon_add.svg" /><span style="margin-left: 4px; color: #458df1;">添加表单</span></label>-->
          </div>
          <div class="option flex-row">
            <label class="label">创建人</label>
            <input type="text" style="border: none; padding: 0;" :value="$root.userInfo.s_username" readonly/>
          </div>
          <div class="option flex-row" style="align-items: center;">
            <label class="label">是否公开项目</label>
            <div>
              <input type="radio" id="yes" name="public" v-model="public" value="1"/>
              <label for="yes" style="width: 40px;margin-right: 32px;">是</label>
            </div>
            <div>
              <input type="radio" id="no" name="public" v-model="public" value="0"/> 
              <label for="no" style="width: 40px;">否</label>
            </div>
          </div>
        </div>
        <div slot="footer" class="flex-row">
          <span class="btn-gray mg-r-8" @click="createProjectDialog = false">取消</span>
          <span class="btn-blue" @click="createProject">保存</span>
        </div>
      </DialogBox>
      <!--end-->
      <!--用户参加的项目卡片列表-->
      <div class="flex-row" style="flex-wrap: wrap">
        <div class="project-card" v-for="project in projectList">
          <router-link :to="'/project/' + project.u_projectID + '/project_home'">
          <!--:class="{project1: project.u_projectID === '1', project2: project.u_projectID === '2'}"-->
            <div class="icon flex-row">
              <i class="iconfont" :class="project.u_projectID === '1' ? 'icon-yiliao' : 'icon-xindiantu'"></i>
            </div>
            <div class="project-text">
              <div class="project-name color87" v-text="project.u_projectName"></div>
              <p class="color54" v-text="project.u_projectMem" style="overflow : hidden; text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical;"></p>
            </div>
          </router-link>
          <div class="delete-project border-t flex-row">
            <span class="color26" v-text="project.u_projectstate === '1' ? '公开' : project.u_projectstate === '0' ? '不公开' : ''"></span>
            <div class="flex-row">
              <span class="color54 mg-r-8" @click="editProject(project.u_projectID)"><i class="iconfont icon-icon-yxj-edit"></i></span>
              <span class="color54" style="cursor: pointer;" @click="deleteProject(project.u_projectID)"><i class="iconfont icon-shanchu"></i></span>
            </div>
          </div>
        </div>
      </div>
      <!--编辑项目对话框-->
      <DialogBox v-if="editProjectDialog">
        <div slot="header">编辑项目</div>
        <div slot="body" class="body">
          <div class="option flex-row">
            <label class="label">项目名称</label>
            <input type="text" v-model="projectInfo.u_projectName"/>
          </div>
          <div class="option flex-row">
            <label class="label">开始时间</label>
            <datepicker class="date" v-model="projectInfo.starttime" :width="128"></datepicker>
            <label class="label" style="width: 104px;">结束时间</label>
            <datepicker class="date" v-model="projectInfo.endtime" :width="128"></datepicker>            
          </div>
          <div class="option flex-row" style="align-items: flex-start">
            <label class="label">项目简介</label>
            <textarea v-model="projectInfo.u_projectMem"></textarea>
          </div>
          <div class="option flex-row">
            <label class="label">任务表单</label>
            <select v-model="projectInfo.u_templeateID" style="background-position: 365px center;">
              <option v-for="form in formList" :value="form.formid">{{form.name}}</option>
            </select>
            <!--<input type="text" class="flex" v-model="filename" readonly/>
            <input type="file" id="editform" @change="handleFile('editform')" hidden/>
            <label class="flex-row" for="editform" style="text-align: left; cursor: pointer;"><div class="btn-gray" style="width: 96px;">修改表单</div></label>-->
          </div>
          <div class="option flex-row">
            <label class="label">创建人</label>
            <input type="text" style="border: none; padding: 0;" :value="$root.userInfo.s_username" readonly/>
          </div>
          <div class="option flex-row" style="align-items: center;">
            <label class="label">是否公开项目</label>
            <div>
              <input type="radio" id="yes" name="public" v-model="projectInfo.u_projectstate" value="1"/>
              <label for="yes" style="width: 40px; margin-right: 32px;">是</label>
            </div>
            <div>
              <input type="radio" id="no" name="public" v-model="projectInfo.u_projectstate" value="0"/> 
              <label for="no" style="width: 40px;">否</label>
            </div>
          </div>
        </div>
        <div slot="footer" class="flex-row">
          <span class="btn-blue mg-r-8" @click="sureEditProject">保存</span>
          <span class="btn-gray" @click="editProjectDialog = false">取消</span>
        </div>
      </DialogBox>
      <!--end-->
    </div>
  </div>
</template>
<script>
  import API from '../api.js'
  import Navigation from '../components/navigation.vue'
  import DialogBox from '../lib/dialog.vue'
  import Datepicker from '../lib/Date.vue'
  export default {
    name: 'Home',
    data () {
      return {
        projectList: [],
        formList: [],
        createProjectDialog: false,
        // filename: '',
        projectName: '',
        startTime: '',
        endTime: '',
        projectMem: '',
        templateID: '',
        public: '0',
        editProjectDialog: false,
        projectInfo: {}
      }
    },
    components: {
      Navigation,
      DialogBox,
      Datepicker
    },
    created () {
      this.getProjectList()
      this.getFormList()
    },
    methods: {
      // handleFile (id) {
        // this.filename = document.getElementById(id).value.substring(document.getElementById(id).value.lastIndexOf('\\') + 1)
      // },
      // 获得项目列表
      getProjectList () {
        this.projectList = []
        API.GetMyProjects({userid: this.$root.userid}).then(response => {
          response.map(n => this.projectList.push(n))
        }).catch(err => {
          console.log(err)
        })
      },
      // 获得表单列表
      getFormList () {
        this.formList = []
        API.GetFormList({userid: this.$root.userid}).then(response => {
          response.map(n => this.formList.push(n))
        }).catch(err => {
          console.log(err)
        })
      },
      // 创建项目
      createProject () {
        API.CreateProject({
          userid: this.$root.userid,
          name: this.projectName,
          templateid: this.templateID,
          projectmem: this.projectMem,
          starttime: this.startTime,
          endtime: this.endTime,
          status: this.public
        }).then(response => {
          this.toast({
            type: 'success',
            text: '新建项目成功！',
            placement: 'top'
          })
          this.getProjectList()
          this.createProjectDialog = false
          this.projectName = ''
          this.startTime = ''
          this.endTime = ''
          this.projectMem = ''
          this.templateID = ''
          this.public = '0'
        }).catch(() => {
          this.toast({
            type: 'error',
            text: '新建项目失败，重新提交试试！',
            placement: 'top'
          })
        })
      },
      // 编辑项目
      editProject (projectid) {
        this.editProjectDialog = true
        this.projectInfo = {}
        API.GetProjectInfo({userid: this.$root.userid, projectid: projectid}).then(response => {
          this.projectInfo = response[0]
          console.log(response)
          this.projectInfo.starttime = this.projectInfo.starttime.substr(0, 10)
          this.projectInfo.endtime = this.projectInfo.endtime.substr(0, 10)
        }).catch((err) => {
          console.log(err)
        })
      },
      // 确认编辑项目
      sureEditProject () {
        this.editProjectDialog = false
        API.EditProject({
          userid: this.$root.userid,
          projectid: this.projectInfo.u_projectID,
          name: this.projectInfo.u_projectName,
          templateid: this.projectInfo.u_templeateID,
          projectmem: this.projectInfo.u_projectMem,
          starttime: this.projectInfo.starttime,
          endtime: this.projectInfo.endtime,
          status: this.projectInfo.u_projectstate
        }).then(response => {
          this.toast({
            type: 'success',
            text: '编辑项目成功！',
            placement: 'top'
          })
          this.getProjectList()
        }).catch(() => {
          this.toast({
            type: 'error',
            text: '编辑项目失败！',
            placement: 'top'
          })
        })
      },
      // 删除项目
      deleteProject (projectid) {
        API.DeleteProject({userid: this.$root.userid, projectid: projectid}).then(response => {
          this.toast({
            type: 'success',
            text: '删除项目成功！',
            placement: 'top'
          })
          this.getProjectList()
        }).catch(() => {
          this.toast({
            type: 'error',
            text: '删除项目失败，重新删除！',
            placement: 'top'
          })
        })
      }
    }
  }
</script>
<style scoped>
  #home .main {
    width: 1016px;
    margin: 56px auto;
  }
  /* 创建新项目对话框 */
  .option{
    margin-bottom: 16px;
    align-items: center;
    display: flex;
    margin-right: 72px;
  }
  .option .label{
    font-size: 14px;
    font-family: -webkit-body;
    margin-right: 24px;
    width: 120px;
    text-align: right;
    color: rgba(0, 0, 0, .54);
  }
  .option input[type=text], .option textarea, .option select{
    width: 384px;
  }
  .option textarea {
    height: 120px;
  }
  /* 项目卡片 */
  .main .project-card {
    width: 238px;
    height: 288px;
    background: #fff;
    border: 1px rgb(0,0,0,.12) solid;
    margin-right: 16px;
    margin-bottom: 16px;
  }
  .project-card .icon{
    height: 164px;
    background-size: cover;
    background-position: center;
    background-color: #41a3fc;
    color: #fff;
    padding: 5px 10px;
    justify-content: center;
    align-items: center;
    font-size: 60px;
  }

  .project-card .project-text {
    height: 90px;
    padding: 12px 16px;
  }
  .project-card .project-text .project-name {
    font-size: 16px;
    margin-bottom: 8px;
  }
  .project-card .delete-project {
    height: 32px;
    line-height: 32px;
    padding: 0 16px;
    justify-content: space-between;
  }

</style>



