<template>
	<div>
		<div class="btn-blue" style="width: 96px;margin-bottom: 32px;" @click="createCenterDialog = true">创建分中心</div>        
		<DialogBox v-if="createCenterDialog">
			<h3 slot="header">创建分中心</h3>
			<div slot="body" class="body">
				<div class="option">
          <label>分中心名称</label>
          <input type="text" v-model="centerName"/>
				</div>
        <div class="option">
          <label >负责人</label>
          <input type="text" v-model="centerLeader"/>
				</div>
				<div class="option">
          <label >联系电话</label>
          <input type="text"  v-model="centerPhone"/>
				</div>
				<div class="option">
          <label >邮箱</label>
          <input type="text" v-model="centerEmail"/>
				</div>
        <div class="option">
          <label >地址</label>
          <input type="text" v-model="centerAdr"/>
				</div>
        <div class="option">
          <label >邮编</label>
          <input type="text" v-model="centerPostcode"/>
				</div>
        <div class="option">
          <label >中心编号</label>
          <input type="text" v-model="centerNum"/>
				</div>
			</div>
			<div slot="footer">
        <div class="btn-blue" @click="createCenter">保存</div>
        <div class="btn-gray" @click="createCenterDialog = false">取消</div>
			</div>
		</DialogBox>
    <!--分中心列表-->
		<div class="card">
			<div class="card-header">分中心列表</div>
			<div class="table">
				<div class="table-tr table-header" style="padding: 0 64px;">
					<div class="w60">ID</div>
					<div class="w160">中心名称</div>
					<div class="w160">负责人</div>
					<div class="w160">中心人数</div>
					<div class="flex">操作</div>
				</div>
				<div class="table-tr" style="padding: 0 64px;" v-for="(center, index) in centerList">
					<div class="w60">{{ index + 1 }}</div>
					<div class="w160">{{ center.u_centername }}</div>
					<div class="w160">{{ center.u_centerlead }}</div>
					<div class="w160">{{ center.userarr.length }}</div>
					<div class="flex flex-row">
						<span class="blue mg-r-20" @click="showCurrentCenterInfo(center)">查看</span>
            <span class="red" @click="deleteCenterDialog = true, currentId = item.u_centerID">删除</span>
					</div>
				</div>
			</div>
		</div>
    <DialogBox v-if="detailDialog">
      <div slot="header" class="flex-row" style="justify-content: space-between; align-items: center;padding-right: 32px;">
        <span>分中心信息</span>
        <div class="btn-gray" style="font-size: 14px;" v-if="!editCenterFlag" @click="editCenterFlag = true">编辑</div>
      </div>
      <div slot="body" style="max-height: 400px; overflow: auto">
        <div class="option">
          <label>分中心名称</label>
          <input type="text" v-model="editCenterInfo.u_centername" :readonly="!editCenterFlag" :style="{border: editCenterFlag ? '1px #ccc solid' : 'none'}"/>
        </div>
        <div class="option">
          <label>负责人</label>
          <input type="text" v-model="editCenterInfo.u_centerlead" :readonly="!editCenterFlag" :style="{border: editCenterFlag ? '1px #ccc solid' : 'none'}"/>
        </div>
        <div class="option">
          <label>分中心电话</label>
          <input type="text" v-model="editCenterInfo.u_centerphone" :readonly="!editCenterFlag" :style="{border: editCenterFlag ? '1px #ccc solid' : 'none'}" />
        </div>
        <div class="option">
          <label>邮箱</label>
          <input type="text" v-model="editCenterInfo.u_centeremail" :readonly="!editCenterFlag" :style="{border: editCenterFlag ? '1px #ccc solid' : 'none'}" />
        </div>
        <div class="option">
          <label>地址</label>
          <input type="text" v-model="editCenterInfo.u_centeradr" :readonly="!editCenterFlag" :style="{border: editCenterFlag ? '1px #ccc solid' : 'none'}" />
        </div>
        <div class="option">
          <label>邮编</label>
          <input type="text" v-model="editCenterInfo.u_centerzipcode" :readonly="!editCenterFlag" :style="{border: editCenterFlag ? '1px #ccc solid' : 'none'}" />
        </div>
        <div class="option">
          <label>中心编号</label>
          <input type="text" v-model="editCenterInfo.u_centernum" :readonly="!editCenterFlag" :style="{border: editCenterFlag ? '1px #ccc solid' : 'none'}" />
        </div>
        <div v-if="editCenterFlag" style="text-align: right;margin-right: 32px;">
          <div class="btn-blue" @click="sureEditCenter">保存</div>
          <div class="btn-gray" @click="cancelEditCenter">取消</div>
        </div>
        <div class="table">
          <div class="table-tr table-header">
            <div class="w60">ID</div>
            <div class="w100">姓名</div>
            <div class="w60">性别</div>
            <div class="w100">年龄</div>
            <div class="flex">任务</div>
          </div>
          <div class="table-tr" v-for="(user, index) in editCenterInfo.userarr">
            <div class="w60">{{ index + 1 }}</div>
            <div class="w100">{{ user.s_username }}</div>
            <div class="w60">{{ user.s_sex === '1' ? '男' : user.s_sex === '2' ? '女' : ''}}</div>
            <div class="w100">{{  }}</div>
            <div class="flex">
              <span>{{ user.task }}</span>
            </div>
          </div>
        </div>
      </div>
      <div slot="footer">
        <div class="btn-gray" @click="clooseDetail">关闭</div>
      </div> 
    </DialogBox>
    <!--删除分中心对话框-->
    <DialogBox v-if="deleteCenterDialog">
      <h3 slot="header">提示信息</h3>
      <div slot="body" class="body">
        您确定要将删除此中心？
      </div>
      <div slot="footer" class="tools">
        <MButton type="blue" @click="deleteCenter">确定</MButton>
        <MButton type="gray" @click="deleteCenterDialog = false">取消</MButton>
      </div>
    </DialogBox>
	</div>
</template>
<script>
  import API from '../api.js'
  import DialogBox from '../lib/dialog.vue'
  export default {
    data () {
      return {
        createCenterDialog: false,
        centerName: '',
        centerLeader: '',
        centerPhone: '',
        centerEmail: '',
        centerAdr: '',
        centerPostcode: '',
        centerNum: '',
        checkedCenterId: '',
        centerList: [],
        currentId: '',
        currentCenterInfo: {},
        editCenterInfo: {
          u_centerID: '',
          u_centername: '',
          u_centerleader: '',
          u_centerphone: '',
          u_centeremail: '',
          u_centerzipcode: '',
          u_centeradr: '',
          u_centernum: '',
          userarr: []
        },
        deleteCenterDialog: false,
        editCenterFlag: false,
        detailDialog: false
      }
    },
    components: {
      DialogBox
    },
    created () {
      this.getCenterList()
    },
    methods: {
      // 获取项目分中心列表
      getCenterList () {
        API.GetProjectCenter({userid: this.$root.userid, projectid: this.$route.params.projectid}).then((response) => {
          this.centerList = response
        }).catch((err) => {
          window.alert(err)
        })
      },
      //
      //
      // 创建分中心
      createCenter () {
        API.CreateCenter({
          userid: this.$root.userid,
          projectid: this.$route.params.projectid,
          centername: this.centerName,
          centerlead: this.centerLeader,
          centerphone: this.centerPhone,
          centeremail: this.centerEmail,
          centeradr: this.centerAdr,
          centerzipcode: this.centerPostcode,
          centernum: this.centerNum
        }).then((response) => {
          this.createCenterDialog = false
          window.alert('创建成功')
          this.centerName = ''
          this.centerLeader = ''
          this.centerPhone = ''
          this.centerEmail = ''
          this.centerAdr = ''
          this.centerPostcode = ''
          this.centerNum = ''
          this.getCenterList()
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 展示当前项目分中心详情
      showCurrentCenterInfo (item) {
        this.detailDialog = true
        this.currentCenterInfo = item
        for (let i in this.currentCenterInfo) {
          this.editCenterInfo[i] = this.currentCenterInfo[i]
        }
      },
      // 确认编辑分中心
      sureEditCenter () {
        API.EditCenter({
          userid: window.sessionStorage.getItem('userid'),
          projectid: this.$route.params.projectid,
          centerid: this.editCenterInfo.u_centerID,
          centername: this.editCenterInfo.u_centername,
          centerlead: this.editCenterInfo.u_centerlead,
          centeradr: this.editCenterInfo.u_centeradr,
          centerphone: this.editCenterInfo.u_centerphone,
          centeremail: this.editCenterInfo.u_centeremail,
          centerzipcode: this.editCenterInfo.u_centerzipcode,
          centernum: this.editCenterInfo.u_centernum
        }).then((response) => {
          this.editCenterFlag = false
          this.toast({
            type: 'success',
            text: '编辑分中心成功！',
            placement: 'top'
          })
        }).catch((err) => {
          this.toast({
            type: 'success',
            text: err,
            placement: 'top'
          })
        })
      },
      // 取消编辑分中心
      cancelEditCenter () {
        this.editCenterFlag = false
        for (let i in this.currentCenterInfo) {
          this.editCenterInfo[i] = this.currentCenterInfo[i]
        }
      },
      /* 删除分中心 */
      deleteCenter () {
        this.deleteCenterDialog = false
        API.DeleteCenter({userid: this.$root.userid, projectid: this.$route.params.projectid, centerid: this.currentId}).then((response) => {
          this.toast({
            type: 'success',
            text: '删除分中心成功！',
            placement: 'top'
          })
          this.getCenterList()
        }).catch(() => {
          this.toast({
            type: 'error',
            text: '删除分中心失败！',
            placement: 'top'
          })
        })
      },
      clooseDetail () {
        this.detailDialog = false
        this.cancelEditCenter()
      }
    }
  }
</script>
<style scoped>
  /* 创建分中心对话框 */
  .option{
    height: 40px;
    align-items: center;
    margin-bottom: 16px; 
    display: flex;
    margin-right: 72px;
  }
  .option label{
    width: 120px;
    text-align: right;
    margin-right: 32px;
    color: rgba(0,0,0,.54);
  }
  .option input[type=text]{
    flex: 1;
  }
  
</style>


