<template>
  <div id="followUp">
    <div class="btn-blue" style="width: 96px;margin-bottom: 32px;" @click="createfollowDialog = true">添加任务</div>
    <!--创建计划对话框-->
    <DialogBox v-if="createfollowDialog">
      <h3 slot="header">创建新随访计划</h3>
      <div slot="body" class="body flex-col">
        <div class="option">
          <label>任务名称</label>  
          <input type="text" v-model="followName" style="flex: 1;"/>
        </div>
        <div class="option">
          <label>任务表单</label>
          <select class="flex" v-model="followForm" style="flex: 1; background-position: 355px center;">
            <option v-for="form in formList" :value="form.formid">{{form.name}}</option>
          </select>
        </div>
        <div class="option">
          <label>条件</label>
          入组后第
          <input type="text" v-model="followMonth" style="width: 40px;">个月
        </div>
        <div class="option" style="align-items: flex-start;">
          <label>任务简介</label>
          <textarea v-model="followMem"style="height: 120px;flex: 1;"></textarea>
        </div>
      </div>
      <div slot="footer" class="footer" style="text-align: right">
        <div class="btn-gray" @click="createfollowDialog = false">取消</div>        
        <div class="btn-blue" @click="createfollow">确定</div>
      </div>
    </DialogBox>
    <!--end-->
    <div class="follow-list-show">
      <div class="follow-list"  v-for="(item,index) in editFollowList" :key="item.taskid">
        <div v-if="!item.edit" class="flex-row tools">
          <div class="btn-gray" @click="item.edit = true">编辑</div>
          <div class="btn-gray flex-row" style="width: 32px; padding-top: 4px;margin-left: 8px;" @click="deleteFollow(item.taskid)"><img src="../assets/icon_svg/icon_delete.svg"/></div>   
        </div>     
        <div class="flex-col">
          <div class="option">
            <label>任务名称</label>  
            <input class="flex" type="text" v-model="item.taskname" :readonly="item.edit ? false : true" :class="{active: !item.edit}"/>
          </div>
          <div class="option">
            <label>任务表单</label>
            <select class="flex" v-model="item.taskformid" :disabled="item.edit ? false : true" :class="{active: !item.edit}">
              <option v-for="form in formList" :value="form.formid">{{form.name}}</option>
            </select>
          </div>
          <div class="option">
            <label style="margin-right: 32px;">条件</label>
            入组后第
            <input type="text" v-model="item.taskmonth" style="width: 40px;" :readonly="item.edit ? false : true" :class="{active: !item.edit}">个月
          </div>
          <div class="option" style="align-items: flex-start">
            <label>任务简介</label>
            <textarea class="flex" v-model="item.taskcontent" :readonly="item.edit ? false : true" :class="{active: !item.edit}" :style="{'padding': item.edit ? '8px' : '1px 8px'}"></textarea>
          </div>
        </div>
        <div v-if="item.edit" style="text-align: right;margin-top: 16px;">
          <div class="btn-gray" @click="cancelEdit">取消</div>        
          <div class="btn-blue" @click="editFollow(item)">确定</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import DialogBox from '../lib/dialog.vue'
  import API from '../api.js'
  export default {
    data () {
      return {
        projectid: this.$route.params.id,
        createfollowDialog: false,
        followName: '',
        followForm: '1',
        followMonth: '',
        followMem: '',
        followList: [],
        editFollowList: [],
        formList: []
      }
    },
    components: {
      DialogBox
    },
    created () {
      this.getFollowList()
      this.getFormList()
    },
    methods: {
      // 获取项目下的随访计划
      getFollowList () {
        API.GetFollows({userid: window.sessionStorage.getItem('userid'), projectid: this.$route.params.projectid}).then((response) => {
          response.map(n => this.followList.push({
            taskid: n.taskid,
            taskname: n.taskname,
            taskformid: n.taskformid,
            taskmonth: n.taskmonth,
            taskcontent: n.taskcontent,
            edit: false
          }))
          response.map(n => this.editFollowList.push({
            taskid: n.taskid,
            taskname: n.taskname,
            taskformid: n.taskformid,
            taskmonth: n.taskmonth,
            taskcontent: n.taskcontent,
            edit: false
          }))
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 创建新随访计划
      createfollow () {
        API.CreateFollow({
          userid: window.sessionStorage.getItem('userid'),
          projectid: this.$route.params.projectid,
          taskname: this.followName,
          taskformid: this.followForm,
          taskmonth: this.followMonth,
          taskcontent: this.followMem
        }).then((response) => {
          this.createfollowDialog = false
          this.getFollowList()
          this.toast({
            type: 'success',
            text: '创建新随访计划成功！',
            placement: 'top'
          })
        }).catch(() => {
          this.toast({
            type: 'error',
            text: '创建新随访计划失败！',
            placement: 'top'
          })
        })
      },
      // 获得表单列表
      getFormList () {
        this.formList = []
        API.GetFormList({userid: this.$root.userid}).then(response => {
          response.map(n => this.formList.push(n))
        }).catch(err => {
          console.log(err)
        })
      },
      // 编辑随访计划
      editFollow (followinfo) {
        API.EditFollow({
          userid: this.$root.userid,
          projectid: this.$route.params.projectid,
          taskid: followinfo.taskid,
          taskname: followinfo.taskname,
          taskformid: followinfo.taskformid,
          taskmonth: followinfo.taskmonth,
          taskcontent: followinfo.taskcontent
        }).then((response) => {
          followinfo.edit = false
          this.toast({
            type: 'success',
            text: '编辑随访成功！',
            placement: 'top'
          })
        }).catch((err) => {
          console.log(err)
          this.toast({
            type: 'error',
            text: '编辑随访失败！',
            placement: 'top'
          })
        })
      },
      // 取消编辑
      cancelEdit () {
        this.editFollowList = []
        this.followList.map(n => this.editFollowList.push({
          projectid: n.projectid,
          taskid: n.taskid,
          taskname: n.taskname,
          taskformid: n.taskformid,
          taskmonth: n.taskmonth,
          taskcontent: n.taskcontent,
          edit: false
        }))
        console.log(this.editFollowList)
      },
      // 删除随访计划
      deleteFollow (taskid) {
        API.DeleteFollow({userid: this.$root.userid, projectid: this.$route.params.projectid, taskid: taskid}).then((response) => {
          this.toast({
            type: 'success',
            text: '删除成功！',
            placement: 'top'
          })
          this.editFollowList = []
          this.followList = []
          this.getFollowList()
        }).catch(() => {
          this.toast({
            type: 'error',
            text: '删除失败！',
            placement: 'top'
          })
        })
      }
    }
  }
</script>
<style scoped>
  /* 创建计划对话框 */
  .option{
    display: flex;
    display: -webkit-flex;
    align-items: center;
    margin-bottom: 16px;
    margin-right: 72px;
  }
  .option label{
    width: 120px;
    margin-right: 32px;
    text-align: right;
    color: rgba(0,0,0,.54);
  }
  .option .active{
    border-color: #fff;
  }
  .option select{
    background-position: 825px center;
  }
  .option select.active{
    background-image: none;
  }
  /* 计划列表显示 */
  .follow-list{
    padding: 16px 0;
    padding-right: 32px;
    background: #fff;
    margin-bottom: 32px;
    position: relative;
  }
  .tools{
    position: absolute;
    right:32px;
  }
</style>


